.goldwidget {
    grid-area: gold;

    width: 25rem;

    margin: auto;
}

.gold {
    text-align: center;
}

.mine {
    font-size: 3rem;

    margin: 0.25rem auto;

    border: 1px solid black;

    box-shadow: 1px 1px black;
}

.ticks {
    position: relative;

    width: 1rem;

    margin: auto;
}

.tick {
    position: absolute;

    top: 0;
    left: 0;
    right: 0;

    margin: auto;
}
